
.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.clock-white {
  font-family:  sans-serif;
  color:#E9E9E9;
  text-shadow: #000 0px 1px 1px;
}
.clock-red {
  font-family:  sans-serif;
  color: #E41400;
  text-shadow: #000 0px 1px 1px;
}
.clock-dark {
  font-family:  sans-serif;
  text-shadow: none
}
.clock-dark-blink {
  font-family:  sans-serif;
  color: #323639;
  text-shadow: none;
}

.digit-red{
  fill: #E41400;
  stroke: #323639;
  stroke-width: 1;
}
.digit-dark{
  fill: #3c4144ea;
  stroke: #323639;
  stroke-width: 1;
}
.css-sqdry3 {
  background:#323639;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
